<template>
  <div class="home">
    <!--    开始页面-->
    <div class="start" :class="{ begin: begin }">
      <button class="begin-button" @click="start">Let ‘s GO</button>
      <p class="tip">你汪提示：</p>
      <p class="tip">阿叽记得开启音量哦</p>
      <p class="tip">PC端食用体验更佳</p>
    </div>
    <!--    <div class="mask"></div>-->
    <!--    背景音乐-->
    <audio ref="audio">
      <source src="https://cdn.jiajicompany.com/bg.mp3" type="audio/mpeg" />
    </audio>
    <!--    内容部分-->
    <section class="content">
      <ul class="bg-list">
        <li class="bg-item" id="bg-item1" :class="{ fade: !stage1 }"></li>
        <li class="bg-item" id="bg-item2" :class="{ fade: !stage2 }"></li>
        <li class="bg-item" id="bg-item3" :class="{ fade: !stage3 }"></li>
        <li class="bg-item" id="bg-item4" :class="{ fade: !stage4 }"></li>
        <li class="bg-item" id="bg-item5" :class="{ fade: !stage5 }"></li>
        <li class="bg-item" id="bg-item6"></li>
      </ul>
      <p
        class="story-content"
        :class="{ fade: !update, content1: firstContent }"
        :style="{ color: storyColor }"
      >
        {{ storyItem }}
      </p>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: "home",
  data() {
    return {
      begin: false,
      update: false,
      storyItem:
        "大二那年在龙之谷认识了一只小叽，得亏阿汪是个女号，不然永远也不会知道小叽还有又腐又污的一面",
      storyIndex: 0,
      storyList: [
        "大二那年在龙之谷认识了一只小叽，得亏阿汪是个女号，不然永远也不会知道小叽还有又腐又污的一面",
        "大三的时候把小叽拖进了天刀，刚入坑的小叽傻白傻白的，然后阿汪又是个甩手掌柜，直接把小叽放养了",
        "阿汪和小叽两只小天香成天正事不干，到处拍照。话说拍照的最好地点莫过于黄昏下的杭州了，只可惜当年电脑太差，拍的画质太渣。",
        "阿汪一直觉得小叽打游戏很菜，没想到小叽天赋异禀，直上秋水，把阿汪打击得不行，从此退隐江湖",

        "不打游戏的时候，两条大咸鱼就开始谈天说地，但是小叽每次都嘲笑阿汪是老年人打字，把阿汪气的不行",
        "小叽时不时会送点小礼物给阿汪，第一次是个便便emmm...，然后还有明信片，衣服什么的",
        "不过明信片每次都收不到，后来小叽再也不给阿汪寄了，该不会是生气了吧，哎...",
        "我学校的小学部·天鹅湖·我走时真的有两只黑天鹅，一只白天鹅，还有一对鸳鸯。觉得这东西会被扔掉，于是不写多了，就酱。生日快乐是第一次，也许会是最后一次，唯一一次。快乐，天天乐。",
        "小叽曾如是写道，还附了句'全球限量发行'",
        "阿汪也想给小叽送礼物，但阿汪无论如何也要不到小叽的地址",

        "和小叽闹矛盾的时候也有，还记得有一次阿汪说错了话，大家不欢而散。正在绞尽脑汁写道歉信的时候，没想到宽宏大量的小叽主动原谅了阿汪",
        "有一段时间大家都忙，阿汪和小叽很默契的没有打扰对方（不会是互相都忘记了8）",
        "听说小叽去了澳洲，阿汪心里好羡慕啊，阿汪也想去国外深造，后悔当初不好好学习，但话说回来，好好学习不打游戏的话岂不就遇不到小叽咯",
        "想当初的小叽傻呆傻呆的，最近似乎改变挺大，别的不说，至少拍照水平更凑合了",

        "以后的小叽会变成什么风格的呢?猜不到，肯定会更漂亮的吧，不过只要看起来没我年轻就好了，诶嘿嘿",

        "说了好些无关紧要的话，本来只是想跟已经长成大叽的小叽说一句:'好好学习呀，别成天刷剧打游戏啦！'",
        "骗你的，不是这句。真正要说的是：",

        "我叽生日快乐，永远快乐",
        "Happy birthday to Lisa’s 24th birthday",
        "你汪完工于2019.10.2"
      ],
      //控制背景图变化
      stage1: true,
      stage2: true,
      stage3: true,
      stage4: true,
      stage5: true,
      storyColor: "#595758",
      firstContent: true
    };
  },
  methods: {
    // 开始
    start() {
      this.begin = true;
      this.$refs.audio.play();
      let stortInter = setInterval(() => {
        this.storyIndex += 1;
        this.update = false;
        setTimeout(() => {
          this.storyItem = this.storyList[this.storyIndex];
          this.update = true;
        }, 1000);
      }, 7000);
      // 7秒时西先去除第一次的样式
      setTimeout(() => {
        this.firstContent = false;
      }, 7000);
      // 第一幕完毕
      setTimeout(() => {
        this.stage1 = false;
        this.storyColor = "#697E92";
      }, 28000);
      // 第二幕完毕
      setTimeout(() => {
        this.stage2 = false;
        this.storyColor = "#585858";
      }, 70000);
      // 第三幕完毕
      setTimeout(() => {
        this.stage3 = false;
      }, 98000);
      // 第四幕完毕
      setTimeout(() => {
        this.stage4 = false;
      }, 105000);
      // 第五幕完毕
      // 最后一幕不移除
      setTimeout(() => {
        clearInterval(stortInter);
        this.stage5 = false;
      }, 140000);
    }
  },
  created() {},
  mounted() {}
};
</script>
